.grid {
	position: relative;
	/* margin: 0 auto;
	padding: 1em 0 4em; */
	list-style: none;
	text-align: center;
}

/* Common style */
.grid figure {
	position: relative;
	float: left;
	overflow: hidden;
	text-align: center;
	cursor: pointer;
}

.grid figure img {
	position: relative;
	display: block;
	min-height: 100%;
	max-width: 100%;
	opacity: 0.8;
}

.grid figure figcaption {
	padding: 2em;
	color: #fff;
	text-transform: uppercase;
	font-size: 1.25em;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
}

.grid figure figcaption::before,
.grid figure figcaption::after {
	pointer-events: none;
}

.grid figure h2 {
  font-size: 40px;
  font-weight: 300;
  font-family: 'poppins', sans-serif;
}

.grid figure h2 span {
	font-weight: 800;
}

.grid figure h2,
.grid figure p {
	margin: 0;
}

.grid figure p {
	letter-spacing: 1px;
	font-size: 68.5%;
}


/*---------------*/
/***** Sadie *****/
/*---------------*/

figure.effect-sadie figcaption::before {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
  height: 100%;

	/* background: -webkit-linear-gradient(top, rgba(72,76,97,0) 0%, rgba(72,76,97,0.8) 75%);
  background: linear-gradient(to bottom, rgba(72,76,97,0) 0%, rgba(72,76,97,0.8) 75%); */
	background: linear-gradient(to bottom, rgba(248,31,1,1) 0%,#f15b25 75%);
	content: '';
  opacity: 0;
 transform: translate3d(0,50%,0);
  border-radius: 15px;
  
}

figure.effect-sadie h2 {
	position: absolute;
	top: 50%;
	left: 0;
	width: 100%;
	color: #484c61;
	transition: transform 0.35s, color 0.35s;
	transform: translate3d(0,-50%,0);
}

figure.effect-sadie figcaption::before,
figure.effect-sadie p {
 transition: opacity 0.35s, transform 0.35s;
  
}
figure.effect-sadie p {
	position: absolute;
	bottom: 25%;
	left: 0;
	padding: 2em;
	width: 100%;
	opacity: 0;
	transform: translate3d(0,10px,0);
}

figure.effect-sadie:hover h2 {
	color: #fff;
	transform: translate3d(0,-50%,0) translate3d(0,-40px,0);
}

figure.effect-sadie:hover figcaption::before ,
figure.effect-sadie:hover p {
	opacity: 1;
	transform: translate3d(0,0,0);
}

@media screen and (max-width: 768px) {

	.grid figure {
		display: inline-block;
		float: none;
		margin: 10px auto;
		width: 100%;
  }
  .grid figure h2 {
    font-size: 14px;
}
  .grid figure p {
    letter-spacing: 1px;
    font-size: 10px;
    line-height: 1;
}
}



